body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: #276baa;
  z-index: -1;
  width: 100vw;
  height: 100vh;
}

.node {
    border: 1px solid rgba(153, 145, 145);
    border-radius: 7px;
    -webkit-user-select: none;
            user-select: none;
}

.empty {
    background-color: white;
}

.wall {
    background-color: rgb(70, 66, 66);
    -webkit-animation-name: wall-anim;
            animation-name: wall-anim;
    -webkit-animation-duration: 0.4s;
            animation-duration: 0.4s;
    z-index: 1;
}

@-webkit-keyframes wall-anim {
    0%{
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    25%{
        -webkit-transform: scale(0.25);
                transform: scale(0.25);
    }
    50%{
        -webkit-transform: scale(0.50);
                transform: scale(0.50);
    }
    75%{
        -webkit-transform: scale(0.75);
                transform: scale(0.75);
    }
    100%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes wall-anim {
    0%{
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    25%{
        -webkit-transform: scale(0.25);
                transform: scale(0.25);
    }
    50%{
        -webkit-transform: scale(0.50);
                transform: scale(0.50);
    }
    75%{
        -webkit-transform: scale(0.75);
                transform: scale(0.75);
    }
    100%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

.start {
    background-color: rgb(0, 255, 34);
}

.end {
    background-color: rgb(255, 58, 58);
}
.visited {
    background-color: rgba(194, 253, 234);
    -webkit-animation-name: visiting;
            animation-name: visiting;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
}

@-webkit-keyframes visiting {
    25% {
        background-color: rgb(166, 212, 196);
    }
    50%{
        background-color: rgb(151, 228, 202);
    }
    75%{
        background-color: rgb(133, 238, 201);
    }
    100%{
        background-color: rgb(73, 250, 191);
    }
}

@keyframes visiting {
    25% {
        background-color: rgb(166, 212, 196);
    }
    50%{
        background-color: rgb(151, 228, 202);
    }
    75%{
        background-color: rgb(133, 238, 201);
    }
    100%{
        background-color: rgb(73, 250, 191);
    }
}

.shortestPath {
    background-color: yellow;
    -webkit-animation-name: shortestPathAnim;
            animation-name: shortestPathAnim;
    -webkit-animation-duration: 0.75s;
            animation-duration: 0.75s;
}

@-webkit-keyframes shortestPathAnim {
    0%{
        -webkit-transform: scale(0.5);
                transform: scale(0.5);
    }
    25%{
        -webkit-transform: scale(1);
                transform: scale(1);
        color: yellowgreen;
    }
    50%{
        trasform: scale(1.25);
        background-color: yellowgreen;
    }
    75%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100%{
        background-color: rgb(223, 223, 49);
    }
}

@keyframes shortestPathAnim {
    0%{
        -webkit-transform: scale(0.5);
                transform: scale(0.5);
    }
    25%{
        -webkit-transform: scale(1);
                transform: scale(1);
        color: yellowgreen;
    }
    50%{
        trasform: scale(1.25);
        background-color: yellowgreen;
    }
    75%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100%{
        background-color: rgb(223, 223, 49);
    }
}
.container {
    display: flex;
    justify-content: center;
}
.grid {
    box-shadow: 7px 7px 5px 5px #124370;
}

.version-row{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    padding: 15px;
}
.version-row > p{
    color: white;
    text-align: end;
}

.time-row {
    margin-top: 0px;
    padding-top: 0px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    padding-right: 15px;
}
.time-row > p {
    color: white;
    text-align: end;
}

  
.header-bar {
    width: 100vw;
    height: 10vh;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    background-color: #0D3B66;
    color: white;
    margin-bottom: 0px;
}

.app-name {
    text-transform: uppercase;
    font-size: large;
    font-weight: bold;
}

.button {
    background-color: rgb(243, 243, 146);
    padding: 5px;
    font-size: medium;
    border-radius: 10px;
}

.start-btn {
    background-color: greenyellow;
}

.selector-label {
    font-size: medium;
    font-weight: bolder;
}

.select {
    padding: 5px;
    border-radius: 10px;
}
.input {
    border-radius: 10px;
    padding: 5px;
    width: 50px
}
